@import "~antd/dist/antd.css";
.ant-tabs-nav {
  margin: 0 !important;
}

.header {
  position: relative;
}

.input_search {
  position: absolute;
  top: 15px;
  left: 250px;
  width: 500px;
  margin-left: 20px;
}

.table_checkbox_group {
  display: flex;
  flex-flow: column;
  justify-content: center;
  margin: 20px;
  width: 120px;
  height: 130px;
  font-size: small;
  text-align: center;
  border: 1px solid #9fb7b9;
  border-radius: 10px;
  position: relative;
}

.table_checkbox_group .content > label {
  cursor: pointer;
  font-size: larger;
  font-weight: bolder;
}

.table_checkbox_group .table_checkbox_group__checkbox {
  display: none;
}
.table_checkbox_group .table_checkbox_group__status {
  width: 12px;
  height: 12px;
  border-radius: 10px;
  position: absolute;
  right: 5px;
  top: 5px;
}

