    background-color: #d5c7bc;
    width: 100%;
    height: 100%;
    border: 1px solid transparent;
    border-radius: 10px;
    position: relative;
}

.table_checkbox_group input[type="checkbox"]:not(:checked) + .content {
    background-color: #fff;
    width: 100%;
    height: 100%;
    border: 1px solid transparent;
    border-radius: 10px;
    position: relative;
} */

.btn_filter_group {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.btn_filter_group .btn_import {
  position: relative;
  top: 5px;
  margin-left: 5px;
}

.btn_filter_group > button {
  margin-top: 5px;
  margin-left: 5px;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  min-height: 500px;
}

.wrapper__list {
  display: flex;
  flex-flow: row;
  flex-wrap: wrap;
}
.pagination {
  position: absolute;
  bottom: 17vh;
}

.wrapper .pagination {
  position: absolute;
  bottom: 20px;
  right: 30px;
}

.tab_content {
  display: flex;
  flex-flow: row wrap;
  width: 100%;
  border: 1px solid #9fb7b9;
  max-height: 450px;
  min-height: 450px;
  border-radius: 5px;
  overflow-y: scroll;
}

.tab_content .tab_content__item {
  display: flex;
  height: 100px;
  align-items: center;
}

.tabs_bill {
  width: 100%;
}

.tab_content .tab_content__item .tab_content__item_btn_delete {
  cursor: pointer;
  width: 30px;
  border: 1px solid #9fb7b9;
  border-radius: 4px;
  padding: 5px;
  margin: 0 10px;
}

.tab_content .tab_content__item .tab__content__item__title {
  width: 200px;
}

.tab_content .tab_content__item .tab__content__item__title span:nth-child(1) {
  display: flex;
  /* flex-flow: column wrap; */
  font-weight: 500;
  font-size: larger;
}

.tab_content .tab_content__item .tab__content__item__title span:nth-child(2) {
  position: relative;
  font-weight: 300;
  font-size: medium;
}

.tab_content
  .tab_content__item
  .tab__content__item__title
  span:nth-child(2)
  img {
  cursor: pointer;
  width: 16px;
  position: absolute;
  top: 1px;
  right: -20px;
}

.tab_content .tab_content__item .tab__content__item__input {
  margin-left: 10px;
  margin-right: auto;
  display: flex;
  flex-flow: row wrap;
}

.tab_content .tab_content__item .tab__content__item__input .input_price {
  width: 80px;
  margin-right: 10px;
}

.tab_content .tab_content__item .tab__content__item__input .input_quantity {
  width: 50px;
  margin: 0 5px;
}

.content_tab {
  position: relative;
}

.bottom_tab {
  position: absolute;
  bottom: 5vh;
}

.wrapper_product {
  border: 1px solid #ccc;
  margin: 5px;
  border-radius: 3px;
}

.product {
  width: 150px;
  min-height: 180px;
  max-height: 200px;
  text-align: center;
  position: relative;
}

.product__image {
  width: 150px;
  height: 150px;
  background-size: cover;
  background-position: center;
}

.product__title {
  margin-top: 10px;
  height: 50px;
  font-weight: 500;
  font-size: medium;
  width: 150px;
  overflow: hidden;
  display: inline-block;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.product__price {
  position: absolute;
  top: 0px;
  right: 5px;
  font-size: large;
  font-weight: 600;
  color: #f76c5e;
}

.form_login {
  display: flex;
  height: 800px;
  flex-flow: column;
}

.form_login .form_login__logo {
  width: 280px;
  height: 100px;
  margin: 0 auto;
  margin-top: 200px;
}

.form_login .form_login__input {
  margin-top: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  max-height: 100vh;
}

.form_login .form_login__input label {
  width: 18vh;
  font-size: medium;
  font-weight: 500;
  font-style: italic;
}

.form_login .form_login__input button {
  width: 100%;
  font-size: medium;
}

.copy_right {
  text-align: center;
  opacity: 0.4;
}

.tool_bill {
  display: flex;
  flex-flow: row wrap;
  margin-bottom: 10px;
}

.bottom_tab {
  display: flex;
}

.btn_group {
  width: 50%;
}

.tool_bill button {
  margin-right: 10px;
  width: 150px;
  height: 70px;
  font-size: small;
  font-weight: 500;
}

.tool_bill .btn_notification {
  background-color: #ffa69e;
  border: 1px solid #ffa69e;
}

.tool_bill .btn_group_bill {
  background-color: #b8f2e6;
  border: 1px solid #b8f2e6;
}

.tool_bill .btn_group_table {
  background-color: #aed9e0;
  border: 1px solid #aed9e0;
}

.account_dropdown {
  position: absolute;
  right: 30px;
  top: 15px;
  width: 150px;
}

.payment_group {
  width: 50%;
}

.payment_group .payment_money {
  display: flex;
  width: 300px;
  justify-content: space-between;
}

.payment_group .payment_money .payment_money_user {
  color: red;
}

.payment_group .input_group {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 10px;
}

.payment_group .input_group label {
  width: 100px;
}

.payment_group .input_group input {
  width: 80px;
  height: 25px;
}

.content_kitchen {
  width: 100%;
  height: 100%;
  min-height: 860px;
  padding: 10px;
  display: flex;
  background-color: rgb(105, 95, 189);
}

.content_kitchen .btn_logOut {
  position: absolute;
  bottom: 30px;
  right: 2.2vh;
}

.sidebar_left,
.sidebar_right {
  width: 50%;
  min-height: 750px;
  max-height: 700px;
  background-color: #fff;
  margin: 10px 10px 0;
  border-radius: 5px 5px 0 0;
}
.sidebar_right .header p {
  background-color: rgba(204, 60, 89, 0.808) !important;
}
.sidebar_left .header p,
.sidebar_right .header p {
  font-weight: 500;
  font-size: 18px;
  text-transform: uppercase;
  background-color: cornflowerblue;
  margin: 0;
  padding: 10px;
  color: #fff;
  border-radius: 5px 5px 0 0;
}
.wrapper_kitchen {
  margin: 10px 0;
  cursor: pointer;
}
.donhang {
  margin: 0 10px;
  background-color: #f5f5f5;
  border-radius: 8px;
  display: flex;
  align-items: center;
}
.donhang p {
  padding: 10px;
  width: 80%;
  font-size: 14px;
  font-weight: 500;
  line-height: 22px;
}
.donhang input {
  width: 20px;
  height: 20px;
  margin: 30px;
}
.soluong button {
  border: 0;
  padding: 5px 10px;
  border-radius: 5px;
}
.no-product p {
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  opacity: 0.8;
}

.product_item {
  margin-top: 10px;
  display: flex;
  align-items: center;
  font-size: medium;
  font-weight: 500;
  height: 70px;
  border: 1px solid #aed9e0;
}

.product_item .product_item__name {
  margin-left: 10px;
  width: 100px;
}

.product_item .product_item__quantity {
  margin-left: auto;
  margin-right: 10px;
}

.procuct__image__requirement {
  margin-left: 5px;
  width: 64px;
}

.product_item .product_item__note {
  color: red;
}

.logo {
  display: flex;
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px;
  color: #fff;
  text-align: center;
  font-size: large;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  cursor: pointer;
  justify-content: center;
}

.logo span {
  margin-left: 10px;
}

.logo image {
  width: 30px;
}

.chart_form {
  display: flex;
  justify-content: center;
  margin-top: 10px;
  margin-bottom: 10px;
}

.chart_form button {
  margin-left: 10px;
}

.button_group {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.button_group button {
  margin: 5px;
}

.form_product label {
  width: 120px;
  font-weight: 500;
}

.wrapper_image {
  margin-top: 10px;
  padding: 3px;
}

.procuct__image {
  width: 150px;
}

.table_type {
  width: 50%;
}

.type_table {
  width: 100%;
  display: flex;
  flex-flow: row;
  height: 270px;
}

.table_comodity {
  width: 100%;
  display: flex;
  flex-flow: row;
  height: 300px;
}

.form_type {
  width: 30%;
  padding: 10px;
  margin-left: 30vh;
}

.import_excel {
  width: 0px;
  height: 0px;
  position: relative;
  left: 10px;
  bottom: -4px;
}

.form_invoice label {
  width: 100px;
}

.input_comodity {
  position: relative;
  display: flex;
  flex-flow: row;
}

.input_comodity .input_comodity__btn_remove {
  width: 40px;
}

.input_comodity .input_comodity__btn_add {
  position: absolute;
  bottom: -32px;
  right: 0px;
}

.type {
  width: 170px;
}

.input_date {
  width: 200px;
  margin-left: 28px;
}

.errorMessage {
  width: 180px;
  position: relative;
  top: -28px;
  right: -100px;
}

.tab__content__item__title .tab__content__item__title__note__input {
  display: flex;
  height: 24px;
  width: 100px;
  flex-flow: row;
}

.procuct__image .ant-image .ant-image-img {
  width: 150px;
  height: 160px;
}

@media (min-width: 992px) and (max-width: 1199.98px) {
  .table_checkbox_group {
    margin: 10px;
    width: 80px;
    height: 80px;
  }

  .table_checkbox_group .content img {
    width: 50px !important;
    height: 50px;
  }

  .tab_content {
    min-height: 300px;
  }

  .btn_group {
    width: 150px;
  }

  .btn_group .select_status {
    width: 140px !important;
  }

  .btn_group .btn_update_status {
    width: 140px !important;
    margin-bottom: 10px;
  }

  .btn_group .tool_bill {
    display: flex;
    flex-direction: row;
  }

  .btn_group .tool_bill button {
    height: 50px;
  }

  .payment_group .input_group {
    width: 310px;
  }

  .tab_content .tabs_bill .bottom_tab {
    bottom: 14vh;
  }

  .wrapper_product {
    width: 100px;
    height: 100px;
    margin-bottom: 60px;
  }

  .product {
    width: 100px;
    min-height: 100px;
  }

  .procuct__image {
    width: 100px;
    height: 100px;
  }

  .procuct__image .ant-image .ant-image-img {
    width: 100px;
    height: 100px;
  }

  .product__title {
    width: 100%;
    height: 25px;
  }

  .tab_content__item_btn_delete {
    margin: 0 5px;
  }

  .tab__content__item__title {
    width: 120px !important;
  }

  .tab__content__item__input {
    margin-left: 0 !important;
    flex-flow: column wrap !important;
    margin-right: 20px !important;
  }

  .tab__content__item__input .input_price {
    display: none;
  }

  .tab__content__item__title__note__input {
    width: 60px !important;
  }
}
